import React, { useEffect, useState } from 'react'

import { useQuery } from 'react-query'

import Layout from 'utils/Layout'
import useStore from 'store/store'

import { Link } from 'react-router-dom'
import { apis } from '../utils/api'

interface IChatRoom {
  chatRoom: number
  email: string
  id: number
  message: string
  nickname: string
  profileImage: string
}

export interface IUserInfo {
  nickName: string | number
}

const ChatMain = () => {
  const getChatList = async () => {
    const { data } = await apis.getChatList(1) //FIXME id .
    return data
  }

  const {
    isLoading,
    error,
    data: roomData,
  } = useQuery<IChatRoom[], Error>('getChatList', getChatList)

  if (error) console.log('"axois", error.message :>> ', error.message)

  // const [name, setName] = useState(1) //FIXME  login  (store) // nickName 1  .
  const { userInfo } = useStore() //  
  const [room, setRoom] = useState('') //TODO 1:1 room  ?

  return (
    <Layout>
      {isLoading ? (
        <>loading </>
      ) : (
        <>
          <div className="m-2 border-2 flex flex-col"> </div>
          {roomData?.map((item: IChatRoom) => {
            //FIXME key 
            return (
              <div className="m-2 border-2 flex flex-col" key={item.chatRoom}>
                <Link
                  onClick={(e) => (!roomData ? e.preventDefault() : null)}
                  to={`/chat?room=${item.chatRoom}&name=${userInfo?.nickName}`} //FIXME msg    
                  // to={`/chat?room=${item.chatRoom}`}
                  // to={`/chat`}
                  // state={{
                  //   room: item.chatRoom,
                  //   name: userInfo?.nickName,
                  //   oppNickname: item.nickname,
                  // }}
                  className="flex"
                >
                  <div className="m-3">
                    <img src={item.profileImage} alt="" />
                  </div>
                  <div>
                    <p> : {item.nickname}</p>
                    <p>  : {item.message} </p>
                  </div>
                </Link>
              </div>
            )
          })}
        </>
      )}
    </Layout>
  )
}

export default ChatMain

